nav {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 1vw 8vw;
    box-shadow: 2px 2px 10px rgba(0, 0, 0, 0.15);

}

nav img {
    width: 150px;
    cursor: pointer;
}

nav .navigation ul {
    display: flex;
    justify-content: flex-end;
    align-items: center;
}

nav .navigation ul li {
    list-style: none;
    margin-left: 50px;
}

nav .navigation ul li a {
    text-decoration: none;
    color: rgb(21, 21, 100);
    font-size: 18px;
    font-weight: 500;
    transition: 0.3s ease;

}

nav .navigation ul li a:hover {
    color: #FDC93B;
}

.para {
    color: #000434;
    text-align: center;
    font-size: 18px;
    font-weight: 500;
}

